.Video {
  width: 100%;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  flex: 1;
  position: absolute;
}

.toolbar-search {
  display: flex;
}

.video-player {
  position: relative;
  width: 100%;
}

.video-container {
  background: black;
  min-height: 300px;
  width: 100%;
  position: fixed;
  width: 50%;
  left: 0;
}

.toolbar-search input {
  flex: 2;
  height: 40px;
  font-size: 18px;
  padding: 5px;
  box-sizing: border-box;
}

.toolbar-search button {
  flex: 0.5;
}

.col_1,
.col_2 {
  flex: 1;
  align-items: stretch;
}

.col_1 {
  background: #333;
  margin-right: 2px;
}

.col_2 {
  margin-left: 2px;
  background: white;
}

.editorClassName {
  padding: 10px 20px;
}

.wrapperClassName{
  background:white;
}
