.Shortcuts {
  background: white;
  height: 100%;
  position: absolute;
  width: 100%;
  background: #333;
  text-align: left;
  box-sizing: border-box;
  padding: 20px;
}

.Shortcuts h3 {
  padding: 0;
  margin: 0;
  margin-bottom: 15px;
  color: white;
}

.Shortcuts-row {
  color: #777;
  font-size: 18px;
  margin-bottom: 10px;
  padding-bottom: 10px;
  border-bottom: 1px solid black;
  display: flex;
  justify-content: space-between;
}
